package strings

import io.github.skeptick.libres.strings.PluralForms
import io.github.skeptick.libres.strings.getCurrentLanguageCode
import kotlin.String
import kotlin.collections.Map

public object MainResStrings {
  private val baseLocale: StringsRu = StringsRu

  private val locales: Map<String, Strings> = mapOf("ru" to StringsRu)

  public val app_title: String
    get() = locales[getCurrentLanguageCode()]?.app_title ?: baseLocale.app_title

  public val header_tab_button_home: String
    get() = locales[getCurrentLanguageCode()]?.header_tab_button_home ?:
        baseLocale.header_tab_button_home

  public val header_tab_button_faq: String
    get() = locales[getCurrentLanguageCode()]?.header_tab_button_faq ?:
        baseLocale.header_tab_button_faq

  public val header_tab_button_privacy: String
    get() = locales[getCurrentLanguageCode()]?.header_tab_button_privacy ?:
        baseLocale.header_tab_button_privacy
}
