package com.dik.gibddhelper.ui

import androidx.compose.foundation.LocalScrollbarStyle
import androidx.compose.foundation.ScrollState
import androidx.compose.foundation.VerticalScrollbar
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxHeight
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.rememberScrollState
import androidx.compose.foundation.rememberScrollbarAdapter
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.foundation.verticalScroll
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.dik.gibddhelper.faq.FaqScreen
import com.dik.gibddhelper.home.HomeScreen
import com.dik.gibddhelper.navigation.Topic
import com.dik.gibddhelper.navigation.getHeaderTopic
import com.dik.gibddhelper.privacy.PrivacyScreen
import com.dik.gibddhelper.theme.AppTheme
import org.w3c.dom.url.URL

@Composable
fun WebPage(url: URL, verticalScrollState: ScrollState = rememberScrollState()) {
    val scrollbarAdapter = rememberScrollbarAdapter(verticalScrollState)
    var selectedTopic by remember { mutableStateOf(url.getHeaderTopic()) }

    AppTheme {
        Box(modifier = Modifier.fillMaxSize()) {
            Column(modifier = Modifier.verticalScroll(verticalScrollState)) {
                Header(defaultTopic = selectedTopic, onTopicChanged = {
                    selectedTopic = it
                })
                Body(
                    when (selectedTopic) {
                        Topic.HOME -> HomeScreen()
                        Topic.FAQ -> FaqScreen()
                        Topic.PRIVACY -> PrivacyScreen()
                        Topic.UNSPECIFIED -> HomeScreen()
                    }
                )
                Footer()
            }

            VerticalScrollbar(
                modifier = Modifier.align(Alignment.CenterEnd).fillMaxHeight(),
                adapter = scrollbarAdapter,
                style = LocalScrollbarStyle.current.copy(
                    thickness = 8.dp, shape = RoundedCornerShape(4.dp)
                ),
            )
        }
    }
}