package com.dik.gibddhelper.ui

import MainRes
import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import androidx.compose.ui.Modifier
import com.dik.gibddhelper.navigation.Topic
import com.dik.gibddhelper.ui.components.HeaderTab

@Composable
fun HeaderNavigationBar(
    defaultTopic: Topic = Topic.HOME,
    navigateToMainPage: () -> Unit,
    navigateToFaq: () -> Unit,
    navigateToPrivacyPage: () -> Unit,
) {
    var selectedTopic by remember {
        mutableStateOf(if (defaultTopic == Topic.UNSPECIFIED) Topic.HOME else defaultTopic)
    }
    val tabs = remember {
        listOf(
            AppTab(
                title = MainRes.string.header_tab_button_home,
                topic = Topic.HOME,
                onClick = navigateToMainPage
            ),
            AppTab(
                title = MainRes.string.header_tab_button_faq,
                topic = Topic.FAQ,
                onClick = navigateToFaq
            ),
            AppTab(
                title = MainRes.string.header_tab_button_privacy,
                topic = Topic.PRIVACY,
                onClick = navigateToPrivacyPage
            ),
        )
    }


    Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.Center) {
        tabs.forEach { appTab ->
            HeaderTab(
                text = appTab.title,
                onClick = {
                    selectedTopic = appTab.topic
                    appTab.onClick()
                },
                isSelected = appTab.topic == selectedTopic
            )
        }
    }
}

private data class AppTab(
    val title: String,
    val topic: Topic,
    val onClick: () -> Unit
)