package com.dik.gibddhelper.navigation

import org.w3c.dom.url.URL

enum class Topic(val title: String) {
    HOME("home"),
    FAQ("faq"),
    PRIVACY("privacy"),
    UNSPECIFIED("unspecified");
}

fun URL.getHeaderTopic(): Topic {
    return this.searchParams.get(UrlParams.TOPIC)?.toHeaderTopic() ?: Topic.UNSPECIFIED
}

private fun String.toHeaderTopic(): Topic? {
    return Topic.values().find { it.title == this }
}