
import com.dik.gibddhelper.ui.WebPage
import kotlinx.browser.window
import org.jetbrains.skiko.wasm.onWasmReady
import org.w3c.dom.url.URL

fun main() {
    onWasmReady {
        BrowserViewportWindow(MainRes.string.app_title) {
            WebPage(url = URL(window.location.href))
        }
    }
}
